import React from 'react';
import { Link } from 'react-router-dom';

import sign from '../../images/about/1.png';

const About = (props) => {
  const ClickHandler = () => {
    window.scrollTo(10, 0);
  };

  return (
    <section className="pt-[120px] pb-[100px]">
      <div className="wraper">
        <div className="grid grid-cols-20 items-center gap-3">
          <div className="col-span-6 md:col-span-12">
            <div className="mb-[20px]">
              <h2 className="text-[#282e3f] text-[32px]">Hakkmzda</h2>
              <p className="text-[#666] text-[15px] leading-[28px] mb-[30px]">
                Biz, mali mavirlik alannda uzmanlam bir ekibiz ve
                mterilerimize geni bir yelpazede finansal zmler sunuyoruz.
                Deneyimli kadromuz, her mterinin benzersiz mali hedeflerini
                anlayarak, onlara zel stratejiler gelitiriyor ve uyguluyor.
                effaflk, drstlk ve gvenilirlik ilkelerimiz dorultusunda,
                mterilerimizin finansal refahn salamak iin alyoruz.
              </p>
              <p className="text-[#666] text-[15px] leading-[28px] mb-[30px]">
                {' '}
                Sektrdeki gncel gelimeleri yakndan takip ediyor ve
                mterilerimizi en iyi ekilde bilgilendirerek, karar
                vermelerine yardmc oluyoruz. Bizimle alan herkes, mali
                konularda desteklenmi hissediyor ve finansal hedeflerine
                ulamak iin gereken destei alyor. Mteri memnuniyeti ve
                baars, bizim iin en nemli nceliklerden biridir.
              </p>
              <div className="mb-[50px] col:mb-[20px]">
                <Link
                  onClick={ClickHandler}
                  to="/about"
                  className="bg-[#c0b596] cursor-pointer text-[16px] font-semibold text-white px-[38px] py-[10px]  capitalize inline-block mt-[6px] transition ease-in-out duration-300 hover:bg-[#d4c291]
                                col:mb-[5px] col:mt-[15px] col:text-[15px] col:px-[18px] col:py-[8px] 
                                "
                >
                  Hakkmzda Daha Fazlas..
                </Link>
              </div>
              <div className="signature">
                <img src={sign} alt="" />
              </div>
            </div>
          </div>
        </div>
      </div>
    </section>
  );
};

export default About;
